<template>
  <main>
    <div class="container">
      <input type="text"  v-model="showNum"  style="direction:ltr;text-align:right" >
      <div>
        <button class="operator"  @click="clean">C</button>
        <button class="plusmines" @click="Reverse">+/-</button>
        <button class="operator"  @click="operator('rem')">rem</button>
        <button class="operator"  @click="operator('/')">÷</button>
        <button  @click="inputNum('7')">7</button>
        <button  @click="inputNum('8')">8</button>
        <button  @click="inputNum('9')">9</button>
        <button class="operator"  @click="operator('*')">×</button>
        <button  @click="inputNum('4')">4</button>
        <button  @click="inputNum('5')">5</button>
        <button  @click="inputNum('6')">6</button>
        <button class="operator"  @click="operator('+')">+</button>
        <button  @click="inputNum('1')">1</button>
        <button  @click="inputNum('2')">2</button>
        <button  @click="inputNum('3')">3</button>
        <button class="operator"  @click="operator('-')">-</button>
        <button style="visibility:hidden"></button>
        <button  @click="inputNum('0')">0</button>
        <button  @click="dot">.</button>
        <button class="equal"  @click="equal()">=</button>
      </div>
    </div>
  </main>
</template>




<script>

export default {
  name:'app',
  data:()=>({
    showNum:'',
    prevNum:[],
    operand:''
  }),
  methods: {
    Reverse(){
      if(this.showNum>0){
        this.showNum = '-' + this.showNum;
      }else{
        this.showNum = -1  * this.showNum;
      }
    },
    clean(){
      this.showNum='';
      this.prevNum=[];
      this.operand='';
    },
    dot(){
      if(this.showNum.includes('.'))return;
      this.showNum+='.';
    },
    inputNum(num){
      if(this.showNum=='' && num==0) return;
      this.showNum+=num;
    },
    operator(operand){
      this.prevNum.push(Number(this.showNum));
      this.operand=operand;
      this.showNum='';
    },

    equal(){
      if(this.operand=='+'){                        //SUM//
          this.prevNum.push(Number(this.showNum));
          let sum = 0;
          for (let i in this.prevNum) {
          sum += this.prevNum[i];
          }  
          this.showNum=sum;
          this.prevNum=[];  
       }


      if(this.operand=='-'){                        //MINUS//
          this.prevNum.push(Number(this.showNum));
          let first = 0;
          first = this.prevNum[0];
          for(let i=1;i<this.prevNum.length;i++){
            first-= this.prevNum[i];
          }
          this.showNum=first;
          this.prevNum=[];  
       }

       if(this.operand=='*'){                        //Multiplication//
          this.prevNum.push(Number(this.showNum));
          let sum = 1;
          for (let i in this.prevNum) {
          sum *= this.prevNum[i];
          }  
          this.showNum=sum;
          this.prevNum=[];  
       }

       if(this.operand=='/'){                        //Division//
          this.prevNum.push(Number(this.showNum));
          let First = this.prevNum[0];
          for (let i=1;i<this.prevNum.length;i++) {
          First /= this.prevNum[i];
          }  
          this.showNum=First;
          this.prevNum=[];  
       }

       if(this.operand=='rem'){                       //Remining//
          this.prevNum.push(Number(this.showNum));
          let result = this.prevNum[0] % this.prevNum[1];
          this.showNum = result;
          this.prevNum=[];
       }

       this.showNum = this.showNum.toString();
    }
  },
  
  
}
</script>

